/* @jsx jsx */
import {jsx} from "@emotion/react"

import {Helmet} from "react-helmet"
import {graphql} from "gatsby"
import PageLayout from "../components/PageLayout";
import Img from "gatsby-image";

import * as styles from "../PageComponents/MarkdownPages/MarkdownPages.styles";
import {spacing32} from "../styles";
import Tags from "../components/Tags/Tags";

const getImageStyle = (aspectRatio: number) => {
    const style = {};

    if (aspectRatio <= 1) {
        // Portrait image
        return {...style, maxHeight: "800px"}
    } else {
        // Landscape image
        return {...style, maxHeight: "405px"}
    }
}

interface BlogPageTemplateProps {
    data: {
        markdownRemark: {
            frontmatter: {
                headerImage: {
                    childImageSharp: {
                        fluid: {
                            base64: string;
                            aspectRatio: number;
                            src: string;
                            srcSet: string;
                            sizes: string;
                        }
                    }
                };
                headerImageAlt: string;
                headerImageCopyright: string;
                pageTitle: string;
                pageDescription: string;
                author: string;
                last_modified: string;
                tags: string[];
            }
            html: string;
        }
    };
}

const MarkdownPageTemplate = (props: BlogPageTemplateProps) => {
    const {data} = props;
    const {markdownRemark} = data
    const {frontmatter, html} = markdownRemark

    const content = <div>
        <div>
            <h1>{frontmatter.pageTitle}</h1>
            <p>
                {frontmatter.author}
            </p>
            <small>
                Last updated: {frontmatter.last_modified}
            </small>
        </div>
        <div css={spacing32}/>
        <div>
            <Img
                fluid={frontmatter.headerImage.childImageSharp.fluid}
                alt={frontmatter.headerImageAlt}
                style={getImageStyle(frontmatter.headerImage.childImageSharp.fluid.aspectRatio)}
                imgStyle={{objectFit: "contain"}}
            />
            {frontmatter.headerImageCopyright.length > 0 &&
                <div css={styles.imageSourceText}>
                    <small>Image by: {frontmatter.headerImageCopyright}</small>
                </div>
            }
        </div>
        <div css={spacing32}/>
        <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{__html: html}}
            css={styles.markdownDiv}
        />
        <div css={spacing32}/>
        <Tags tags={frontmatter.tags}/>
    </div>

    return (
        <div>
            <Helmet>
                <meta charSet="utf-8"/>
                <meta name="viewport" content="width=device-width, initial-scale=1"/>
                <title>Arshi Annafi | {frontmatter.pageTitle}</title>
                <meta name="description" content={frontmatter.pageDescription}/>
            </Helmet>
            <PageLayout content={content}/>
        </div>
    )
}

export default MarkdownPageTemplate;

export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        headerImage {
          childImageSharp {
            fluid(maxHeight: 500) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        headerImageAlt
        headerImageCopyright
        pageTitle
        pageDescription
        author
        last_modified(formatString: "YYYY-MM-DD")
        tags
      }
    }
  }
`
