/* @jsx jsx */
import {jsx} from "@emotion/react"

import {Link} from "gatsby";

import * as styles from "./Tags.styles";

interface TagsProps {
    tags: string[]
}

const Tags = (props: TagsProps) => {
    const {tags} = props;

    return <div>
        {tags.map(tag => {
            return <Link to={`/tags/${tag}`} key={tag} css={styles.links}>
                {tag}
            </Link>
        })}
    </div>
}

export default Tags
